import React from "react";
import { Button } from "@material-ui/core";

export const SaveButton = () => {
  const saveAsPDF = () => {
    fetch("https://sibi-app.us.auth0.com/api/v2/", { mode: "cors" })
      .then((res) => res.json)
      .then((res) => console.log(res));
  };

  return (
    <Button variant="contained" color="primary" onClick={saveAsPDF} fullWidth>
      Save
    </Button>
  );
};
